body {
  font-family: 'Archivo Black', 'tenor Sans',-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}
.header-image {
  background: url('/assets/images/city-blue.png') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.is-outlined {
  background-color:transparent;
  color:#fff;
}
.hero-cta {
  padding: 30px 0;
}
.panel-block.section p {
  font-size: 17px;
  line-height: 1.4;
  color:#95A5A6;
}
.section.main {
  background-color:#F0F0F0;
}
.icon-block {
  font-size:5em;
}

.button.is-warning {
  background-color: #ECBD47;
  border-color: transparent;
  color: rgba(17, 17, 17, 1);
}
.footer {
  background-color: #ECBD47;
}

.footer .nav {
  background-color: inherit;
}

.grey-background {
  background-color: #5F6871;
  color: #F9E3A3;
}

.grey-background .title {
  color: #F9E3A3;
}

.button.is-dark {
  background-color: #1A2634;
  border-color: transparent;
  color: #F9E3A3;
}

.hero.is-primary .title, .hero.is-primary .subtitle {
  color: #1A2634;
}

@media screen and (max-width: 768px) {
  .hero.is-primary .nav-menu {
    background-color: #5F6871;
  }
}